import React from "react";
import prof from "./Profile.module.css";
import ProfileStatus from "./ProfileStatus";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userNotFoundImage from "../../assets/images/userNotFound.jpeg"

const MyProfile = ({profileInfo, status, setChangeProfileStatus, isOwner, saveProfilePhoto, ...props}) => {
    debugger;
    const savePhoto = (event) => {
        debugger;
        if (event.target.files.length) {
            saveProfilePhoto(event.target.files[0])
        }
    }

    return (
        <div>
            <div>
                <img className={prof.img} src={isOwner ? profileInfo?.photos?.small : userNotFoundImage}/>
                <div>
                    <label htmlFor="file">Download avatar:</label>
                    <input type="file" id="file" name="file" multiple onChange={savePhoto} />
                </div>
            </div>
            <div>
                <ProfileStatusWithHooks status={status} changeStatus={setChangeProfileStatus}/>
            </div>
            <div className={prof.about}>+
                <div>About me: {profileInfo?.aboutMe}</div>
                <div>Full name: {profileInfo?.fullName}</div>
            </div>
        </div>
    )
}

export default MyProfile